<template>
  <li v-bind="$attrs" v-for="product in productsNormalized" :key="product.id">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img :src="product.image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#" @click.prevent="openQuickWiew(product.id)">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price"> {{ product.pricePretty }} ₽ </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in product.colors" :key="color.id">
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            name="color"
            :value="color.id"
            v-model="id"
          />
          <span
            class="colors__value"
            :style="{ 'background-color': color.code }"
          >
          </span>
        </label>
      </li>
    </ul>
  </li>
  <BaseModal v-model:open="isQuickWiewOpen">
    <ProductQuickWiew :product-id="currentProductId" />
  </BaseModal>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import BaseModal from "@/components/BaseModal.vue";
import { defineAsyncComponent, h } from "vue";

export default {
  inheritAttrs: false,
  components: {
    BaseModal,
    ProductQuickWiew: defineAsyncComponent({
      loader: () => import("@/components/ProductQuickWiew.vue"),
      delay: 0,
      loadingComponent: () => h("div", "Загрузка..."),
    }),
  },
  name: "ProductItem",
  computed: {
    isQuickWiewOpen: {
      get() {
        return !!this.currentProductId;
      },
      set(isOpen) {
        if (!isOpen) {
          this.currentProductId = null;
        }
      },
    },
    productsNormalized() {
      return this.products.map((product) => {
        return {
          ...product,
          pricePretty: numberFormat(product.price),
        };
      });
    },
  },
  props: ["products"],
  methods: {
    gotoPage,
    openQuickWiew(productId) {
      this.currentProductId = productId;
    },
  },
  data() {
    return {
      color: "#73B6EA",
      currentProductId: null,
    };
  },
};
</script>
